.MainSection {
    display: grid;
    grid-template-rows: 1fr;
    grid-gap: 10px;
    align-content: center;
    justify-content: center;
}
  
.CategoriesH {
font-family: Permanent Marker, cursive;
margin: 0
}

.PostersH {
font-family: Permanent Marker, cursive;
margin: 0
}

.MovieContainer {
border-style: solid;
border-width: 1px;
border-radius: 5%;
height: 600px;
width: 420px;
overflow-y: scroll;
margin-left: 10px;
margin-right: 10px;
display: grid;
grid-template-columns: 1fr;
grid-template-rows: 40;
margin-bottom: 20px;
margin-top: 20px;
box-shadow: 10px 15px 35px #888888;
padding-top: 20px;
background-color: #F3EED9;
}

@media screen and (min-width: 890px) {
    .MainSection {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 10px;
        grid-column-gap: 20px;
        align-content: center;
        justify-content: center;
    }
    .CategoriesH {
        font-family: Permanent Marker, cursive;
        margin: 0;
        grid-column: 1;
    }
    .ListSection {
        grid-column: 1;
        align-self: center;
        justify-self: center;
    }
    .PostersH {
        font-family: Permanent Marker, cursive;
        margin: 0;
        grid-column: 2;
        grid-row: 1;
    }
    .MovieSection {
        grid-column: 2;
        justify-self: center;
    }
    .MovieContainer {
        border-style: solid;
        border-width: 1px;
        border-radius: 1.5%;
        height: 1470px;
        width: 420px;
        overflow-y: scroll;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 40;
        margin-top: 20px;
        box-shadow: 10px 15px 35px #888888;
        padding-top: 20px;
        background-color: #F3EED9;
    }
}